/**
 * Back function.
 */
function moveBack () {
    var item;

    if ( historyStack.length === 0 ) {
        app.route(app.pages.main);

        return;
    }

    item = historyStack.pop();
    types.active = item.type;
    playList.setData({focusIndex: item.focus, data: item.data});
    playText = item.title;
    infoPanel.title.links.$name.innerText = playText;
}


/**
 * Prepare object to play.
 *
 * @param {Object} data - data to play
 * @param {Object} config - config to prepare
 */
function prepareToPlay ( data, config ) {
    var selectFile = require('../utils/select.file'),

        activeItem = data || playList.$focusItem.value,
        name = infoData.name,
        languages = [],
        hasNext = false,
        hasPrevios = false,
        previosHandler = null,
        nextHandler = null,
        // playerContext = config && config.context,
        // intent = config && config.intent,
        item;

    config = config || {};

    function checkFile () {
        var file;

        file = selectFile(playList.data.slice(1), languages, config.intent.data.file);

        if ( file ) {
            playList.focusIndex(file.index);
            prepareToPlay(file.value, config);
        } else {
            config.playerContext = null;
            config.intent.close();
        }
    }

    function selectNextFile ( code ) {
        var needTitle = true;

        languages = config.playerContext.metadata.audioLang;
        app.pages.main.saveVideo(function () {
            refreshData(true);
        }, config, true);

        switch ( types.active ) {
            case types.file:
                moveBack();
                needTitle = false;
            /* eslint no-fallthrough: 0 */
            case types.series:
                playList.move(code);
                if ( needTitle ) {
                    playText = historyStack[historyStack.length - 1].title;
                }
                pressOk(checkFile);
                break;
            /* eslint no-fallthrough: 2 */
        }
    }

    function closeIntent () {
        app.pages.main.saveVideo(refreshData, config);
        config.playerContext = null;
        config.intent.close();
    }

    if ( infoData['is_tv_series'] ) {
        switch ( types.active ) {
            case types.file:
                item = historyStack[historyStack.length - 1];
                hasNext = item.focus + 1 < item.data.length;
                hasPrevios = item.focus > 1;
                break;
            case types.series:
                hasNext = playList.$focusItem.index + 1 < playList.data.length;
                hasPrevios = playList.$focusItem.index > 1;
                break;
        }

        if ( hasNext ) {
            nextHandler = function () {
                selectNextFile(keys.down);
            };
        }
        if ( hasPrevios ) {
            previosHandler = function () {
                selectNextFile(keys.up);
            };
        }
    }

    playHistory = {
        data: playList.data,
        focus: playList.$focusItem.index,
        type: types.active
    };

    /**
     * Play object.
     *
     * @param {boolean} error - have some error
     * @param {string} uri - uri to play
     */
    function play ( error, uri ) {
        if ( error ) {
            return;
        }

        if ( infoData['is_tv_series'] && historyStack.length ) {
            name += ' ' + seasonTitle + ' ' + playText;
        }


        function startPlay ( position ) {
            var season, episode;

            if ( infoData['is_tv_series'] && historyStack.length ) {
                episode = historyStack[historyStack.length - 1];
                episode = episode.data[episode.focus];
                season = historyStack[0];
                season = season.data[season.focus];
            }

            config.intent = core.intent({
                action: 'play',
                mime: 'content/video',
                data: {
                    title: name,
                    uri: uri,
                    movie: infoData,
                    file: activeItem,
                    season: season,
                    episode: episode,
                    position: position,
                    subtitles: activeItem.subtitleList,
                    mediaId: activeItem.id
                },
                events: {
                    end: function () {
                        app.pages.main.saveVideo(refreshData, config, true);

                        if ( nextHandler ) {
                            nextHandler();

                            return;
                        }
                        config.playerContext = null;
                        config.intent.close();
                    },
                    error: closeIntent,
                    stop: closeIntent,
                    hide: closeIntent,
                    next: nextHandler,
                    prev: previosHandler,
                    close: function () {
                        if ( config.playerContext ) {
                            app.pages.main.saveVideo(refreshData, config);
                            config.playerContext = null;
                        }
                    }
                },
                context: config.playerContext
            }, function ( error, context ) {
                debug.fail('Play error', error);
                config.playerContext = context;
                if ( DEVELOP ) {
                    window.context1 = context;
                }
            });
        }

        if ( activeItem.watched_time && activeItem.watched_status === 'not_ended' && !config.playerContext ) {
            panelSet.blur();
            playModal.show({file: activeItem, movie: infoData, callback: startPlay, name: name});

            if ( config.intent ) {
                config.intent.close();
            }

            return;
        }

        startPlay(0);
    }

    if ( activeItem.url ) {
        play(false, activeItem.url);
    } else {
        infoData.getUrl(activeItem.id, play);
    }
}


/**
 * Add/remove to/from favorites.
 */
function favorite () {
    var favoriteLinks;

    if ( TARGET === 'android-stb' || TARGET === 'android-app' ) {
        favoriteLinks = leftList.links.favorite.links;
    }

    if ( infoData.favorite ) {
        $favorite.style.display = 'none';
        if ( TARGET === 'android-stb' || TARGET === 'android-app' ) {
            favoriteLinks.$icon.className = 'theme-icon theme-icon-favorite';
            favoriteLinks.$title.innerText = gettext('Add to favorite');
        }
    } else {
        $favorite.style.display = 'block';
        if ( TARGET === 'android-stb' || TARGET === 'android-app' ) {
            favoriteLinks.$icon.className = 'theme-icon theme-icon-favorite-active active';
            favoriteLinks.$title.innerText = gettext('Remove from favorites');
        }
    }

    app.emit('change:favorite', {
        video: infoData,
        callback: function ( error ) {
            if ( error ) {
                if ( infoData.favorite ) {
                    $favorite.style.display = 'block';
                    if ( TARGET === 'android-stb' || TARGET === 'android-app' ) {
                        favoriteLinks.$icon.className = 'theme-icon theme-icon-favorite-active active';
                        favoriteLinks.$title.innerText = gettext('Remove from favorites');
                    }
                } else {
                    $favorite.style.display = 'none';
                    if ( TARGET === 'android-stb' || TARGET === 'android-app' ) {
                        favoriteLinks.$icon.className = 'theme-icon theme-icon-favorite';
                        favoriteLinks.$title.innerText = gettext('Add to favorite');
                    }
                }
            }
        }
    });
}


