/**
 * Click function.
 *
 * @param {Function} [callback] - result handler
 */
pressOk = function ( callback ) {
    var activeItem = playList.$focusItem.value,
        list = [];

    if ( DEVELOP ) {
        window.h = historyStack;
    }
    switch ( types.active ) {
        case types.file:
            prepareToPlay();
            break;

        case types.season:
            historyStack.push({
                type: types.season,
                data: playList.data,
                title: playText,
                focus: playList.$focusItem.index
            });
            playText = playList.$focusItem.data.items[0].value;
            seasonTitle = playText;
            infoPanel.title.links.$name.innerText = seasonTitle;
            configRequest.seasonId = activeItem.id;
            infoData.getSeries(activeItem.id, function ( error, data ) {
                list = error ? [] : prepareSeasons(data, true);
                list.unshift({
                    items: [
                        {
                            className: 'theme-icon theme-icon-back'
                        },
                        {
                            className: 'title',
                            value: '. .'
                        }

                    ],
                    value: null
                });
                types.active = types.series;
                playList.setData({focusIndex: 0, data: list});
                if ( callback ) {
                    callback();
                }
            });
            break;

        case types.series:
            historyStack.push({
                type: types.series,
                data: playList.data,
                title: seasonTitle,
                focus: playList.$focusItem.index
            });
            configRequest.episodeId = activeItem.id;
            playText = playList.$focusItem.data.items[0].value;
            infoData.getEpisode(configRequest, function ( error, data ) {
                list = error ? [] : prepareFiles(infoData, data.files);

                if ( list.length === 1 && !callback ) {
                    prepareToPlay(list[0].value);
                    historyStack.pop();

                    return;
                }

                infoPanel.title.links.$name.innerText = playText;
                list.unshift({
                    items: [
                        {
                            className: 'theme-icon theme-icon-back'
                        },
                        {
                            className: 'title',
                            value: '. .'
                        }

                    ],
                    value: null
                });
                types.active = types.file;
                playList.setData({focusIndex: 0, data: list});
                if ( callback ) {
                    callback();
                }
            });
            break;
    }
};


refreshData = function ( noRefresh ) {
    var item;

    switch ( types.active ) {
        case types.file:
            if ( playHistory.type !== types.active || playHistory.data !== playList.data || noRefresh ) {
                return;
            }

            item = playHistory.data[playHistory.focus];
            switch ( item.value.watched_status ) {
                case 'watched':
                    item.items[1].className = 'theme-icon theme-icon-ok';
                    break;
                case 'not_watched':
                    item.items[1].className = 'theme-icon theme-icon-ok hidden';
                    break;
                case 'not_ended':
                    item.items[1].className = 'theme-icon theme-icon-ok disabled';
                    break;
            }
    }
    playHistory = null;
    refreshPlayList();
};


refreshPlayList = function () {
    if ( playList.data.length && playList.$focusItem ) {
        playList.setData({data: playList.data, focusIndex: playList.$focusItem.index});
    }
};


descriptionItem = new TabItem({
    propagate: true,
    focusable: true,
    events: {
        focus: function () {
            textArea.focus();
        },
        keydown: function ( event ) {
            var name = infoData.name,
                subtitleList = [],
                file, files, intent, playerContext, index;

            //Play focus item.
            function play ( error, url ) {

                function closeIntent () {
                    app.pages.main.saveVideo(null, {intent: intent, playerContext: playerContext});
                    playerContext = null;
                    intent.close();
                }

                function startPlay ( position ) {
                    intent = core.intent({
                        action: 'play',
                        mime: 'content/video',
                        data: {
                            title: name,
                            uri: url,
                            movie: infoData,
                            file: file,
                            position: position,
                            externalSubtitles: subtitleList,
                            mediaId: file.id
                        },
                        events: {
                            end: function () {
                                app.pages.main.saveVideo(null, {intent: intent, playerContext: playerContext}, true);
                                playerContext = null;
                                intent.close();
                            },
                            error: closeIntent,
                            stop: closeIntent,
                            hide: closeIntent,
                            next: null,
                            prev: null,
                            close: function () {
                                if ( playerContext ) {
                                    app.pages.main.saveVideo(null, {intent: intent, playerContext: playerContext});
                                }
                            }
                        },
                        context: playerContext
                    }, function ( error, context ) {
                        debug.fail('Playing error', error);
                        playerContext = context;
                    });
                }

                if ( error ) {
                    return;
                }


                if ( file.watched_status === 'not_ended' && file.watched_time ) {
                    panelSet.blur();
                    playModal.show({file: file, movie: infoData, callback: startPlay});

                    if ( intent ) {
                        intent.close();
                    }

                    return;
                }

                startPlay(0);
            }

            switch ( event.code ) {
                case keys.ok:
                    leftList.focusIndex(1);
                    infoPanel.title.links.$name.innerText = playText;
                    playItem.show();
                    infoPanel.focus();
                    if ( !infoData['is_tv_series'] && playList.data.length === 1 ) {
                        prepareToPlay();
                    }
                    break;

                case keys.playPause:
                    if ( infoData['is_tv_series'] ) {
                        if ( playList.data.length ) {
                            if ( infoData.last_watched_season_id ) {
                                for ( index = 0; index < playList.data.length; index++ ) {
                                    if ( infoData.last_watched_season_id === playList.data[index].value.id ) {
                                        playList.focusIndex(index);
                                        break;
                                    }
                                }
                            }
                            pressOk(function () {
                                var selectedIndex = 1;

                                if ( playList.data.length > 1 ) {
                                    if ( infoData.last_watched_episode_id ) {
                                        for ( index = 1; index < playList.data.length; index++ ) {
                                            if ( infoData.last_watched_episode_id === playList.data[index].value.id ) {
                                                selectedIndex = index;
                                                break;
                                            }
                                        }
                                    }
                                    playList.focusIndex(selectedIndex);

                                    pressOk(function () {
                                        if ( playList.data.length > 1 ) {
                                            selectedIndex = 1;
                                            if ( infoData.last_watched_file_id ) {
                                                for ( index = 1; index < playList.data.length; index++ ) {
                                                    if ( infoData.last_watched_file_id === playList.data[index].value.id ) {
                                                        selectedIndex = index;
                                                        break;
                                                    }
                                                }
                                            }
                                            playList.focusIndex(selectedIndex);
                                            playItem.show();
                                            infoPanel.focus();
                                            pressOk();
                                        } else {
                                            playItem.show();
                                            infoPanel.focus();
                                        }

                                    });
                                } else {
                                    playItem.show();
                                    infoPanel.focus();
                                }
                            });
                        }
                    } else {
                        if ( infoData['last_watched_file_id'] ) {
                            for ( index = 0; index < infoData.files.length; index++ ) {
                                if ( infoData['last_watched_file_id'] === infoData.files[index].id ) {
                                    file = infoData.files[index];
                                    if ( file.url ) {
                                        play(false, file.url);
                                    } else {
                                        infoData.getUrl(file.id, play);
                                    }

                                    return;
                                }
                            }
                        }
                        files = sortQuality(infoData, infoData.files);
                        subtitleList = files.subtitles;
                        files = files.video;
                        if ( files.length ) {
                            file = files[0];
                        } else {
                            return;
                        }
                        if ( file.url ) {
                            play(false, file.url);
                        } else {
                            infoData.getUrl(file.id, play);
                        }
                    }
                    break;
            }
        }
    }
});


