playModal = new ModalMessage({
    visible: false,
    title: gettext('Play'),
    className: 'width2x',
    events: {
        show: function ( data ) {
            this.configData = data;
            this.children[0].focus();
            this.$text.innerText = data.name;
            modalPlayList.links.continueWatch.links.$title.innerText = gettext('Continue watching from') + ' ' +
                parseTime(data.file.watched_time);
        }
    },
    children: [
        modalPlayList = new LayoutList({
            focusIndex: 0,
            size: 2,
            cycle: true,
            fixedData: true,
            className: 'padded',
            data: [
                {
                    name: 'continueWatch',
                    items: [
                        {
                            name: '$title',
                            className: 'title',
                            value: gettext('Continue watching from')
                        }
                    ],
                    click: function () {
                        if ( playModal.configData.callback ) {
                            playModal.configData.callback(playModal.configData.file.watched_time);
                        }
                    }
                },
                {
                    items: [
                        {
                            className: 'title',
                            value: gettext('Watch again')
                        }
                    ],
                    click: function () {
                        if ( playModal.configData.callback ) {
                            playModal.configData.callback(0);
                        }
                    }
                }
            ],
            events: {
                'click:item': function () {
                    playModal.hide();
                    panelSet.focus();
                },
                keydown: function ( event ) {
                    LayoutList.prototype.defaultEvents.keydown.call(this, event);
                    switch ( event.code ) {
                        case keys.left:
                            event.stop = true;
                            break;
                        case keys.back:
                            playModal.hide();
                            panelSet.focus();
                            event.stop = true;
                            break;
                    }
                }
            }
        })
    ]
});
page.add(playModal);


