page.saveVideo = function ( callback, data, end ) {
    var activeItem = filmList.$focusItem.data,
        item, position, duration,
        progress, config;

    function add ( error, statusData ) {
        var index;

        if ( !error && statusData && statusData.status ) {
            item.file['watched_status'] = config['watched_status'];
            item.movie['watched_status'] = statusData['movie_watched_status'];
            switch ( item.movie['watched_status'] ) {
                case 'watched':
                    activeItem.items[3].className = 'theme-icon theme-icon-ok';
                    if ( item.episode ) {
                        item.episode.items[1].className = 'theme-icon theme-icon-ok';
                    }
                    break;

                case 'not_watched':
                    activeItem.items[3].className = 'theme-icon theme-icon-ok hidden';
                    if ( item.episode ) {
                        item.episode.items[1].className = 'theme-icon theme-icon-ok hidden';
                    }
                    break;

                case 'not_ended':
                    activeItem.items[3].className = 'theme-icon theme-icon-ok disabled';
                    if ( item.episode ) {
                        item.episode.items[1].className = 'theme-icon theme-icon-ok disabled';
                    }
                    break;
            }

            if ( statusData['season_watched_status'] && item.season ) {
                item.season.value['watched_status'] = statusData['season_watched_status'];
                switch ( item.season.value['watched_status'] ) {
                    case 'watched':
                        item.season.items[1].className = 'theme-icon theme-icon-ok';
                        break;

                    case 'not_watched':
                        item.season.items[1].className = 'theme-icon theme-icon-ok hidden';
                        break;

                    case 'not_ended':
                        item.season.items[1].className = 'theme-icon theme-icon-ok disabled';
                        break;
                }
            }

            if ( item.episode ) {
                for ( index = 0; index < item.episode.value.files.length; index++ ) {
                    if ( item.episode.value.files[index].id === item.file.id ) {
                        item.episode.value.files[index]['watched_status'] = config['watched_status'];
                        if ( config['watched_time'] ) {
                            item.episode.value.files[index]['watched_time'] = config['watched_time'];
                        }
                        item.episode.value['watched_status'] = config['watched_status'];
                        switch ( config['watched_status'] ) {
                            case 'watched':
                                item.episode.items[1].className = 'theme-icon theme-icon-ok';
                                break;

                            case 'not_watched':
                                item.episode.items[1].className = 'theme-icon theme-icon-ok hidden';
                                break;

                            case 'not_ended':
                                item.episode.items[1].className = 'theme-icon theme-icon-ok disabled';
                                break;
                        }
                        break;
                    }
                }
            }

            if ( config['watched_time'] ) {
                item.file['watched_time'] = config['watched_time'];
            }

            page.refreshList();
            if ( callback ) {
                callback();
            }
        }

    }

    if ( data.intent.data && data.playerContext ) {
        position = data.playerContext.metadata.position;
        duration = data.playerContext.metadata.duration;
        item = data.intent.data;
        if ( end ) {
            config = {
                'watched_status': 'watched',
                'watched_time': duration - 10
            };
            item.movie.changeWatchedFile(item.file.id, config, add);
        } else if ( position > 10 ) {
            if ( position && duration ) {
                progress = position / duration * 100;
                if ( progress > 95 ) {
                    config = {
                        'watched_status': 'watched',
                        'watched_time': position - 10
                    };
                } else if ( progress > 5 ) {
                    config = {
                        'watched_status': 'not_ended',
                        'watched_time': position - 10
                    };
                }
                item.movie.changeWatchedFile(item.file.id, config, add);
            }
        }

    }
};


page.refreshList = function ( check ) {
    var item;

    if ( check ) {
        item = filmList.$focusItem.data;
        switch ( item.value['watched_status'] ) {
            case 'watched':
                item.items[3].className = 'theme-icon theme-icon-ok';
                break;
            case 'not_watched':
                item.items[3].className = 'theme-icon theme-icon-ok hidden';
                break;
            case 'not_ended':
                item.items[3].className = 'theme-icon theme-icon-ok disabled';
                break;
        }
    }

    if ( filmList.data.length && filmList.$focusItem ) {
        filmList.setData({data: filmList.data, focusIndex: filmList.$focusItem.index});
    }
};


