core.addListeners({
    show: function ( event ) {
        switch ( event.source ) {
            case core.SOURCES.LAUNCHER:
                if ( app.api.config.search ) {
                    return;
                }

                refreshList();
                // app.api.playerContext = null;
                break;
        }
    },

    intent: function ( intent, callback ) {
        if ( intent.action === 'videoClub' ) {
            switch ( intent.mime ) {
                case 'info':
                    if ( intent.data ) {
                        if ( app.pages.info === app.activePage ) {
                            app.pages.info.emit('show', {
                                data: {
                                    info: intent.data,
                                    type: 'intent',
                                    callback: callback
                                }
                            });
                        } else {
                            app.route(app.pages.info, {
                                info: intent.data,
                                type: 'intent',
                                callback: callback
                            });
                        }
                        callback(false, null);
                    } else {
                        callback(true, null);
                    }

                    break;

                default:
                    app.route(app.pages.main);
                    callback(false, null);
                    break;
            }
        }
    }
});


module.exports = page;
